.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body,html{background-color:#ffffff}
body{color:#333}
input,video{display:block}
video:hover{cursor:pointer}
.input-file{padding:10px;background-color:#aaa}
.file-picker-info{font-size:.9em}
.video-container{position:relative;border:1px solid #555;background-color:#222;padding-top:0;padding-left:0;padding-right:0}
.video-holder{position:relative}
.video-holder video{padding-bottom:16px}
.video-title{position:absolute;bottom:0;font-size:.8em;color:#eee}
#side-container{padding-right:0}
.current{background-color:#9bc2f7;color:#000}
#timestamp-info{font-size:.8em}
.new-highlight{font-weight:700;font-size:1.1em}
.timestamp-list-container{position:relative}
.timestamp-list-container2{position:absolute}
.timestamp-list{border:1px solid #555;max-height:200px;overflow:auto}
.timestamp-list ul{list-style-type:none;padding-left:0}
.timestamp-list ul li{font-size:.8em;padding-left:10px}
.timestamp-list ul li:hover{cursor:pointer}
#primary-view-container{display:none}
#control-buttons{padding:10px}
#control-buttons button{margin-right:10px}
#play-pause-btn{width:70px}
#seek-bar{width:100%}
.info{font-size:.9em}
#cookie-disclaimer{margin-top:20px;font-size:.8em}
.error{background-color:red;color:#fff}
.alert-warning{color:#222;border-color:#aaa}
.hide{display:none}
.feedback{font-size:.9em;padding-top:40px}
.bg-custom{background-color:#3576af}
.bg-black{background-color:#222}
.header{background-color:#3576af;border-bottom:1px solid #111;margin-bottom:8px}
.header .header-text{padding-bottom:12px;color:#eee}
footer{margin-top:20px;font-size:.8em;text-align:center}


